.home-page {
    grid-column: 2;

    .text-zone {
        position: relative;
        left: 0%;
        top: 40%;
        transform: translateY(-50%);
        width: 50%;
        max-height: 90%;
        z-index: 1;
        white-space: nowrap; // Add this line to prevent line breaks

        @media screen and (max-width: 500px) {
            left: 0%;
            top: 35%;
        }
        
        .hi-zone {
            
        }
        
    }

    .separator {
        margin-right: 6px;
    }

    h1 {
        color: #fff;
        font-size: 70px;
        margin: 0;
        font-family: 'Coolvetica';
        font-weight: 400;

        

        &::before {
            content: '<h1>';
            font-family: 'La Belle Aurore';
            color: rgba(137, 137, 255, 0.5);
            font-size: 25px;
            position: absolute;
            margin-top: -20px;
            left: -40px;
            animation: fadeIn 3.1s 2s backwards;
            opacity: 0.6;
            @media screen and (max-height: 600px), (max-width: 800px) {
                left: 0px;

            }
        }

        &::after {
            content: '</h1>';
            font-family: 'La Belle Aurore';
            color: rgba(137, 137, 255, 0.5);
            font-size: 25px;
            position: absolute;
            margin-top: 73px;
            margin-left: 20px;
            animation: fadeIn 2s 3s backwards;
            opacity: 0.6;

            @media screen and (max-width: 800px) {
                margin-top: 60px;
            }
            @media screen and (max-height: 600px), (max-width: 800px) {
                margin-top: 30px;

            }
        }

        img {
            width: 45px;
            opacity: 0;
            height: auto;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;

            &:first-of-type {
                margin-left: 20px;
            }

            @media (max-width: 800px) {
                width: 40px;
            }

            @media (max-width: 500px) {
                width: 25px;
                &:first-of-type {
                    margin-left: 10px;
                }
            }

        }

        @media (max-width: 800px) {
            font-size: 55px;
            &::before,
            &::after {
                font-size: 20px;
            }
        }

        @media (max-width: 500px) {
            font-size: 35px;
            // &::before,
            // &::after {
            //     // visibility: hidden;
            // }
        }
    }

    h2 {
        color: #8d8d8d;
        margin-top: 20px;
        font-weight: 400;
        font-size: 16px;
        font-family: sans-serif;
        letter-spacing: 3px;
        animation: fadeIn 1s 1.8s backwards;
        z-index: 1;

        @media screen and (max-width: 680px) {
            font-size: 10px;
            letter-spacing: 1.5px;
            font-weight: 500;
        }
    }

    .flat-button-services {
        display: flex;
        background: rgba(137, 137, 255, 0.15);
        color: #a7a7cf;
        font-size: 15px;
        font-weight: 500;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 12px 20px;
        border: 1px solid rgba(137, 137, 255, 0.3);
        margin-top: 25px;
        float: left;
        animation: fadeIn 1s 1.8s backwards;
        white-space: nowrap;
        margin-right: 15px;
        height: 30px;
        text-align: center;
        align-items: center;
        justify-content: center;
        width: 71%;
        box-shadow: 0 0 15px rgba(137, 137, 255, 0.1);
        backdrop-filter: blur(5px);
        transition: all 0.3s ease;

        &:hover {
            background: rgba(137, 137, 255, 0.25);
            color: #fff;
            border-color: rgba(137, 137, 255, 0.5);
            box-shadow: 0 0 25px rgba(137, 137, 255, 0.2),
                        inset 0 0 15px rgba(137, 137, 255, 0.15);
            transform: translateY(-2px);
        }

        &:active {
            transform: translateY(1px);
            box-shadow: 0 0 15px rgba(137, 137, 255, 0.1),
                        inset 0 0 10px rgba(137, 137, 255, 0.1);
        }

        @media screen and (max-width: 500px) {
            font-size: 11px;
            padding: 5px 10px;
        }
    }

    .flat-button-contact {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: #a7a7cf;
        font-size: 15px;
        font-weight: 500;
        letter-spacing: 4px;
        height: 30px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 12px 20px;
        border: 1px solid rgba(137, 137, 255, 0.3);
        margin-top: 10px;
        float: left;
        animation: fadeIn 1s 1.8s backwards;
        white-space: nowrap;
        width: 71%;
        background: rgba(137, 137, 255, 0.05);
        box-shadow: 0 0 15px rgba(137, 137, 255, 0.1);
        backdrop-filter: blur(5px);
        transition: all 0.3s ease;

        &:hover {
            background: rgba(137, 137, 255, 0.15);
            color: #fff;
            border-color: rgba(137, 137, 255, 0.5);
            box-shadow: 0 0 25px rgba(137, 137, 255, 0.2),
                        inset 0 0 15px rgba(137, 137, 255, 0.15);
            transform: translateY(-2px);
        }

        &:active {
            transform: translateY(1px);
            box-shadow: 0 0 15px rgba(137, 137, 255, 0.1),
                        inset 0 0 10px rgba(137, 137, 255, 0.1);
        }

        @media screen and (max-width: 500px) {
            font-size: 11px;
            padding: 5px 10px;
        }
      
    }
    .flat-button {
        color: #a7a7cf;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 10px 18px;
        border: 1px solid #a7a7cf;
        margin-top: 25px;
        float: left;
        animation: fadeInAnimation 1s 1.8s backwards;
        white-space: nowrap;
        background: rgba(137, 137, 255, 0.05);
        box-shadow: 0 0 15px rgba(137, 137, 255, 0.1);
        backdrop-filter: blur(5px);
        transition: all 0.3s ease;

        &:hover {
            background: rgba(137, 137, 255, 0.15);
            color: #fff;
            box-shadow: 0 0 25px rgba(137, 137, 255, 0.2),
                        inset 0 0 15px rgba(137, 137, 255, 0.15);
            border-color: rgba(137, 137, 255, 0.5);
            transform: translateY(-2px);
        }

        &:active {
            transform: translateY(1px);
            box-shadow: 0 0 15px rgba(137, 137, 255, 0.1),
                        inset 0 0 10px rgba(137, 137, 255, 0.1);
        }
    }
}
